.error-msg-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 60%;
    padding: 2em 0;
}

.error-msg-container>* {
    margin-bottom: 1em;
}
.error-msg-container>*:last-child {
    margin-bottom: 0;
}